import React from 'react';

import {Footnotes, Footnote, Ref, Code} from '../util.js';

export default function() {
    return <>
        <p>Moreover, for small \(\epsilon\), we have the excellent approximation
        {' \\(\\tan{p} \\sim \\epsilon^{-1}\\)'}.
        Requiring {' \\(\\tan{p} > p\\)'} then imposes the condition \(p \epsilon \in [0, 1)\), or</p>
        \[p \cdot \left[(2k + 1) \, \pi - 2p\right] \in [0, 2).\]
        <p>Summing up the above, we want rational approximations to \(\pi\) that are a bit too small, but <i>exceedingly</i> close.<Ref id={1}/>
        How do we go about looking for such approximations? The continued fraction expansion, of course!</p>
        <Code language='python'>{`def continued_fraction_coefficients():
    """Read the continued fraction coefficients of pi from a file."""
    # Left as an exercise to the reader

def rational_approximations():
    """Generate the sequence of best rational approximants of pi."""
    # We use the isomorphism between SL_2(R) and the fractional linear transformations.
    # We start with the function 1/x, and repeatedly pre-compose
    # with 1/(a + x), where a is the continued fraction coefficient.
    # We return the limit of the current rational function as x → ∞.
    n0, n1 = 0, 1
    d0, d1 = 1, 0
    for a in continued_fraction_coefficients():
        (n0, n1) = (n1, a * n1 + n0)
        (d0, d1) = (d1, a * d1 + d0)
        yield (n1, d1)
`}</Code>
        <p>For each rational approximation \(n/d\) with \(n = 2p\) even and \(d = 2k+1\) odd, we’d like to check if \(p \cdot (d \pi - 2p) \in [0, 2)\). But since we’re going to be working with huge numbers, we want to avoid floating point math if possible.</p>
        <p>One way to accomplish this is to replace the \(\pi\) in this check with an even <i>better</i> rational approximation \(n' / d'\).
        For this check to be accurate, we’d like \(d' \gg p \cdot d\).
        The condition then becomes</p>
        <p className='emphasis'>
            {String.raw`\[0 \leq p \cdot (dn' - 2pd') < 2d',\]`}
        </p>
        <p>which only involves integer math. Summing up the above:</p>
        <Code language='python' start={17}>{`import itertools

# List will the first several rational approximations of pi.
approx = []
gen = rational_approximations()

for i in itertools.count():
    # Fill up our list with rational approximations.
    while len(approx) <= i: approx.append(next(gen))

    # We want n/d with n even, d odd.
    n, d = approx[i]
    if n % 2 != 0 or d % 2 == 0: continue
    p = n // 2

    # We'd like d' >> pd; I found that d' >= 4pd works fine.
    threshold = 4 * p * d
    while approx[-1][1] < threshold: approx.append(next(gen))
    nn, dd = approx[-1]

    # Our integer-math-only check for an 'interesting' value of p.
    if 0 <= p * (d*nn - 2*p*dd) < 2*dd:
        # At this point, we should have tan(p) > p.
        # All that remains is a primality check:
        if is_probable_prime(p):
            print(p)
`}</Code>
        <p>If you’re following along, you might want to add more logging just to see what the code is doing. All that remains is to define a reasonable probabilistic primality test <code>is_probable_prime</code>. Personally, I got the best performance by doing trial division on primes up to a million, then calling <a href='https://sourceforge.net/projects/openpfgw/'>PFGW</a> via <code>subprocess</code>. If you want to stay within Python, the <a href='https://pypi.org/project/gmpy2/'>gmpy2</a> library is also a reasonable option:</p>
        <Code language='python'>{`from gmpy2 import is_strong_prp, is_prime

PRIMES = [k for k in range(1, 1000000) if is_prime(k)]
def is_probable_prime(n):
    if any(n % p == 0 for p in PRIMES):
        return False
    return is_strong_prp(n, 3)
`}</Code>

        <p>The verdict? In less than a second, we reproduce the 46-digit prime mentioned at the start of the blog post.
        In two or three seconds, we find a 1017-digit prime solution (which Matt <a href='https://twitter.com/standupmaths/status/1296400194721001472'>mentioned</a>),
        which begins and ends as</p>
        \[p = 230835870782\dots 610326005069.\]
        <p>And after a few hours, we find the monstrous 35,085-digit prime</p>
        \[p = 409461998988\dots 829370185991,\]
        <p>which, at the time of writing, I believe Matt (and, perhaps, everybody else in the world) is unaware of.</p>
        <p>I think that's enough for today.</p>

        <Footnotes>
        <Footnote id={1}>More precisely, we want an approximation accurate to the order of denominator squared. The constants are a bit too small to apply <a href='https://en.wikipedia.org/wiki/Dirichlet%27s_approximation_theorem'>Dirichlet’s approximation theorem</a> directly, so we aren’t guaranteed infinitely many solutions (although I suspect there are).</Footnote>
        </Footnotes>
    </>;
}
